import Vue from 'vue'
import App from './App.vue'
import store from './store'

import slider from './components/slider.vue'
import toast from './components/toast.vue'
import uploader from './components/uploader.vue'
import popbox from './components/popbox.vue'
import icon from './components/icon.vue'
import widget from './plugins/widget'

import './app.scss'

// 
Vue.component('slider', slider)
Vue.component('toast', toast)
Vue.component('uploader', uploader)
Vue.component('popbox', popbox)
Vue.component('icon', icon)

/**
 * 
 * ! 
 */
window.$communicator = new Vue()

const vuePageDesigner = {
  name: 'vue-page-designer',
  store,
  props: {
    value: Object,
    widgets: Object,
    widgetStyle: Object
  },
  components: { App },
  created () {
    //  widgets
    // TODO:  widgets  widgetStyle
    Vue.use(widget, {
      widgets: this.widgets,
      widgetStyle: this.widgetStyle
    })
    // 
    if (this.value) {
      store.replaceState(this.value)
    }
    window.$communicator.$on('save', () => {
      this.$emit('save', store.state)
    })
  },
  render (h) {
    return h('App')
  }
}

export default vuePageDesigner



